
.appBaseView-card-container {
    background: #ECECEC;
    padding: 30px;
    display: flex;
    flex-wrap: wrap;
}

@media only screen and (min-width: 1024px) {
    .appBaseView-card {
        width: 30%;
        margin-top: 20px;
        margin-right: auto;
    }

    .appBaseView-store-plugins-container {
        padding: 20px 20px;
        width: 95%;
        margin: auto;

    }
}

@media only screen and (min-width: 400px) and (max-width: 1024px) {
    .appBaseView-card {
        width: 90%;
        padding-top: 10px;
        margin: auto;
    }

    .appBaseView-store-plugins-container {
        padding-top: 40px;
        width: 80%;
        margin: auto;

    }
}

@media only screen and (max-width: 400px) {
    .card {
        width: 90%;
        padding-top: 10px;
        margin: auto;
    }

    .store-plugins-container {
        padding-top: 40px;
        width: 80%;
        margin: auto;

    }
}