.aspect-ratio {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 75%;
}

.aspect-ratio iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.card-container {
    background: #ECECEC;
    padding: 30px;
    display: flex;
    flex-wrap: wrap;
}

@media only screen and (min-width: 1024px) {
    .card {
        width: 30%;
        margin-top: 20px;
        margin-right: auto;
    }

    .store-plugins-container {
        padding-top: 40px;
        width: 60%;
        margin: auto;

    }
}

@media only screen and (min-width: 400px) and (max-width: 1024px) {
    .card {
        width: 90%;
        padding-top: 10px;
        margin: auto;
    }

    .store-plugins-container {
        padding-top: 40px;
        width: 80%;
        margin: auto;

    }
}

@media only screen and (max-width: 400px) {
    .card {
        width: 90%;
        padding-top: 10px;
        margin: auto;
    }

    .store-plugins-container {
        padding-top: 40px;
        width: 80%;
        margin: auto;

    }
}